import React from "react";
import contactMe from "./contactMe.png";
import safe from "./staysafe.png";

function Contact() {
  return (
    <main>
      <section id="showcase">
        <div>
          <img src={contactMe} align="center" alt="Smiley face" height="100" />
          <h2>A friendly hello or kind suggestions</h2>
          <form className="contact-form">
            <input
              name="name"
              type="text"
              className="form-control"
              placeholder="Your Name"
              required
            />
            <br />
            <input
              name="email"
              type="email"
              className="form-control"
              placeholder="Your Email"
              required
            />
            <br />
            <textarea
              name="message"
              className="form-control"
              placeholder="Message"
              row="8"
            ></textarea>
            <br />
            <input
              type="submit"
              className="form-control submit"
              value="SEND MESSAGE"
            />
          </form>
        </div>
      </section>
      <div className="comment">
        <img src={safe} alt="Stay safe" height="126" />
      </div>
    </main>
  );
}

export default Contact;
