import React from "react";
import Chess from "./Chess";
import spa from "./spa.jpg";

function Sample() {
  const sampleCode = `componentDidMount() {
    fetch("https://qunliw.github.io/chess.json")
      .then((resp) => resp.json())
      .then((chessResp) => {
        this.setState({ chessData: chessResp });
      });
  }`;
  return (
    <main>
      <Chess />
      <div className="comment">
        <img src={spa} alt="Single Page Application (SPA)" height="150" />
      </div>
    </main>
  );
}

export default Sample;
