import React from "react";
import { NavLink } from "react-router-dom";

function Header() {
  return (
    <header>
      <div calssName="branding">
        <h2>
          <span class="highlight">Qunli's</span> World
        </h2>
      </div>
      <nav>
        <NavLink exact activeClassName="current" to="/">
          Home
        </NavLink>
        <NavLink exact activeClassName="current" to="/about">
          About
        </NavLink>
        <NavLink exact activeClassName="current" to="/projects">
          Projects
        </NavLink>
        <NavLink exact activeClassName="current" to="/sample">
          Sample
        </NavLink>
        <NavLink exact activeClassName="current" to="/contact">
          Contact
        </NavLink>
      </nav>
      <div className="covid">
        <button
          onClick={() => {
            window.open(
              "https://qunliw.github.io/us-covid19/",
              "Popup",
              "toolbar=no, location=no, statusbar=no, menubar=no, scrollbars=1, resizable=0, width=580, height=600, top=30"
            );
          }}
        >
          COVID Live!
        </button>
      </div>
    </header>
  );
}

export default Header;
